.services2-top {
  padding: 70px 0;
  background-image: url("../img/services-bg.webp");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__logo {
    display: flex;
    align-items: center;
    &-img {
      width: 150px;
      height: 150px;
      img {
        width: 100%;
        height: auto;
      }
    }
    &-desc {
      margin-left: 30px;
    }
    &-name {
      font-family: 'Neue Einstellung';
      font-weight: 700;
      font-size: 36px;
      line-height: 120%;
    }
    &-info {
      display: inline-flex;
      align-items: center;
      background: #323248;
      border-radius: 5px;
      padding: 6px 8px;
      margin-top: 10px;
      span {
        font-size: 14px;
        line-height: 120%;
        margin-left: 5px;
      }
    }
  }
  &__socials {
    display: flex;
    align-items: center;
  }
  &__social {
    width: 50px;
    height: 50px;
    background: rgba(22, 22, 40, 0.5);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    &:not(:first-child) {
      margin-left: 10px;
    }
    img {
      max-width: 26px;
      height: auto;
    }
  }
}