.btn-blue {
  font-size: 14px;
  line-height: 120%;
  color: #FFFFFF;
  background: #45456D;
  border: 1px solid transparent;
  border-radius: 30px;
  &:hover {
    background: transparent;
    border: 1px solid #45456D;
  }
}
.btn-violet {
  font-size: 14px;
  line-height: 120%;
  color: #FFFFFF;
  background: #6333B1;
  border: 1px solid transparent;
  border-radius: 50px;
  &:hover {
    background: transparent;
    border: 1px solid #6333B1;
  }
}
.btn-border {
  font-size: 18px;
  line-height: 120%;
  color: #AE7CFF;
  border: 1px solid #AE7CFF;
  border-radius: 50px;
  &:hover {
    background: #AE7CFF;
    color: #ffffff;
  }
}
.display3 {
  font-size: 24px;
  line-height: 120%;
  font-weight: 500;
  color: #FFFFFF;
}
.display2 {
  font-weight: 500;
  font-size: 32px;
  line-height: 120%;
  color: #FFFFFF;
}
.navigation {
  padding: 50px;
  &__list li {
    font-size: 24px;
    line-height: 120%;
    &:not(:first-child) {
      margin-top: 25px;
    }
  }
  &__btn {
    font-size: 24px;
    width: 250px;
    height: 50px;
    margin-top: 25px;
  }
}
.nice-select {
  min-width: 160px;
  width: 100%;
  height: 34px;
  background: #323248 !important;
  border: unset;
  border-radius: 60px;
  display: flex;
  align-items: center;
  padding: 0 30px 0 12px;
  &:after {
    z-index: 1;
    top: 50%;
    right: 12px;
    transform: translate(0, -50%);
    width: 10px;
    height: 6px;
    background-image: url("../img/arrow-select.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
  .current {
    font-family: 'Rubik';
    font-weight: 400;
    font-size: 16px;
    line-height: 105%;
    color: #FFFFFF;
  }
  .list {
    border-radius: 0;
    border: unset;
    li {
      font-family: 'Rubik';
      font-weight: 400;
      font-size: 16px;
      line-height: 105%;
      background: #323248;
      border: unset;
      color: #FFFFFF;
      padding: 10px;
    }
  }
}
.nice-select .option:hover,
.nice-select .option.focus,
.nice-select .option.selected.focus {
  background: #242440;
}
.nice-select.open:after {
  transform: rotate(180deg) ;
}
.form {
  &-group {
    input {
      background: #323248;
      border-radius: 7px;
      font-size: 14px;
      line-height: 120%;
      border: transparent;
      color: #ffffff;
      padding: 0 20px;
      &::placeholder {
        font-size: 14px;
        line-height: 120%;
        color: #7D7D97;
      }
    }
    span {
      display: block;
      font-size: 16px;
      line-height: 120%;
    }
  }
  &-radio {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    &:not(:last-child) {
      margin-right: 10px;
    }
    .radio {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      margin: 0;
      opacity: 0;
      cursor: pointer;
      &:checked~label {
        background: #6333B1;
      }
    }
    label {
      min-width: 108px;
      font-size: 14px;
      line-height: 120%;
      background: #323248;
      border-radius: 60px;
      padding: 10px 15px;
    }
  }
  &-check {
    position: relative;
    display: flex;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100% !important;
      height: 100% !important;
      margin: 0;
      opacity: 0;
      &:checked~label:before {
        opacity: 1;
        pointer-events: all;
      }
    }
    label {
      font-size: 16px;
      line-height: 140%;
      padding-left: 30px;
      a {
        display: block;
        color: #AE7CFF;
        position: relative;
        z-index: 3;
        line-height: 140%;
      }
      &:after {
        content: '';
        position: absolute;
        top: -2px;
        left: 0;
        width: 20px;
        height: 20px;
        border: 1px solid #7D7D97;
        border-radius: 5px;
      }
      &:before {
        content: '';
        position: absolute;
        top: -1px;
        left: 0;
        width: 20px;
        height: 18px;
        background-image: url("../img/check-icon.svg");
        background-repeat: no-repeat;
        background-position: center;
        opacity: 0;
        pointer-events: none;
      }
    }
  }
}
.select2 {
  width: 100% !important;
  &-selection {
    height: 50px;
    background: #323248 !important;
    display: flex !important;
    align-items: center;
    border-radius: 7px !important;
    &--multiple {
      border: 1px solid #323248 !important;
    }
    &:focus {
      border: 1px solid #323248;
    }
    textarea {
      color: #ffffff;
    }
  }
  &-dropdown {
    border: unset;
    background: transparent;
  }
 &-results__options {
   border-radius: 7px;
   max-height: 186px !important;
   &::-webkit-scrollbar {
     width: 3px;
   }
   &::-webkit-scrollbar-track {
     background: #ffffff;
     border-radius: 3px;
   }
   &::-webkit-scrollbar-thumb {
     background: #323248;
     border-radius: 3px;
   }
 }
  &-results__option {
    background: #333347;
    height: 42px;
    padding: 0 20px;
    display: flex;
    align-items: center;
  }
  &-results__option--selectable {
    background: #333347 !important;
  }
  &-results__option--selected {
    background-image: url("../img/check-icon3.svg") !important;
    background-repeat: no-repeat !important;
    background-position:top 50% right 20px !important;
    background-color: #49495F !important;
  }
}
.select2-container--default .select2-selection--multiple {
  padding: 0 20px;
  overflow-x: auto;
  &::-webkit-scrollbar {
    height: 3px;
  }
  &::-webkit-scrollbar-track {
    background: #ffffff;
    border-radius: 3px;
  }
  &::-webkit-scrollbar-thumb {
    background: #323248;
    border-radius: 3px;
  }
}
.select2-container .select2-selection--multiple .select2-selection__rendered {
  display: flex;
  align-items: center;
}
.select2-container--default .select2-selection--multiple .select2-selection__choice {
  min-width: 70px;
  height: 30px;
  background: #49495F;
  border-radius: 40px;
  color: #ffffff;
  border: 1px solid transparent;
  margin: 0;
  padding: 5px 25px 5px 10px;
  display: flex;
  align-items: center;
  position: relative;
  &:not(:first-child) {
    margin-left: 10px;
  }
  button {
    position: absolute;
    top: 50%;
    left: calc(100% - 24px);
    transform: translate(0, -50%);
    width: 14px;
    height: 14px;
    background-image: url("../img/select-close.svg");
    background-repeat: no-repeat;
    background-position: center;
    border-right: none;
    &:hover {
      background-color: transparent;
    }
    span {
     opacity: 0;
    }
  }
  span {
    font-size: 14px;
    line-height: 105%;
  }
}
.select2-search {
  width: 100%;
}
.select2-container--default .select2-search--inline .select2-search__field {
  margin: 0;
}
.modal {
  background: rgba(0, 0, 0, 0.4);
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  overflow-y: auto;
  z-index: 1000;
  opacity: 0;
  pointer-events: none;
  &--active{
    opacity: 1;
    pointer-events: unset;
  }
  @media screen and (min-width: 993px) {
    text-align: center;
    &:before {
      display: inline-block;
      vertical-align: middle;
      content: " ";
      height: 100%;
    }
    &__dialog {
      display: inline-block;
      text-align: left;
      vertical-align: middle;
    }
  }
  &__dialog{
    width: 700px;
    max-width: 100%;
    position: relative;
    background: #1F1F31;
    box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.25);
    border-radius: 15px;
    padding: 90px 50px;
    margin: 0 auto;
    &:after {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(0, -50%);
      width: 330px;
      height: 330px;
      border-radius: 50%;
      background: radial-gradient(50% 50% at 50% 50%, rgba(117, 74, 239, 0.7) 0%, rgba(31, 31, 49, 0) 100%);
      pointer-events: none;
    }
    @media screen and (max-width: 992px) {
      margin: 50px auto 0;
      max-width: calc(100% - 30px);
    }
  }
  &__close {
    position: absolute;
    top: 30px;
    right: 30px;
    width: 40px;
    height: 40px;
    background: #45456D;
    border-radius: 50%;
  }
  &__content {
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }
  &__desc {
    max-width: 260px;
    margin: 0 0 0 auto;
    margin-left: 50px;
  }
  &__title {
    font-weight: 500;
    font-size: 20px;
    line-height: 120%;
  }
  &__text {
    font-size: 16px;
    line-height: 120%;
    color: #CBCBEA;
    margin-top: 5px;
  }
  &__img {
    width: 391px;
    position: absolute;
    z-index: 2;
    left: -40px;
    bottom: -40px;

    img {
      width: 100%;
     height: auto;
    }
  }
}

@media screen and (max-width: 992px) {
  .modal {
    &__dialog {
      &:after {
        left: 50px;
        width: 200px;
        height: 200px;
      }
    }
    &__content {
      justify-content: space-between;
    }
    &__img {
      width: 250px;
      position: static;
    }
  }
}

@media screen and (max-width: 768px) {
  .display3 {
    font-size: 20px;
  }
  .display2 {
    font-size: 24px;
  }
  .form {
    &-check {
      label {
        font-size: 14px;
      }
    }
    &-group {
      span {
        font-size: 14px;
      }
    }
  }
}

@media screen and (max-width: 650px) {
  .modal {
    &__dialog {
      padding: 100px 30px 30px;
      &:after {
        display: none;
      }
    }
    &__content {
      flex-direction: column;
    }
    &__img {
      width: 300px;
      margin: 50px auto 0;
    }
    &__desc {
      max-width: 100%;
      width: 100%;
      margin: 0;
      order: -1;
    }
  }
}

@media screen and (max-width: 450px) {
  .select2-container--default .select2-search--inline .select2-search__field {
   font-size: 12px;
  }
  .modal {
    &__img {
      width: 200px;
    }
  }
}